import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { GoogleApiWrapper } from 'google-maps-react';
import GoogleMapsContainer from './GoogleMapsContainer';

import {Button, List, ListItem, ListItemText, TextField} from '@material-ui/core/';

import SpotifyWebApi from 'spotify-web-api-js';
const spotifyApi = new SpotifyWebApi();
const apiKey = 'r0iwmceSJAq5Ynt1';

class Main extends Component {
constructor(props) {
  super(props);
  const params = this.getHashParams();
  this.state = {
    artistInfo: {
      names: []
    },
    artistLookUp: {
      id: '',
      name: '',
      type: '',
      city: '',
      lat: null,
      lng : null
    }
  }

  if(params.access_token) {
    spotifyApi.setAccessToken(params.access_token)
  }
}

componentWillMount() {
  this.getTopArtists();
}

  getHashParams() {
    var hashParams = {};
    var e, r = /([^&;=]+)=?([^&;]*)/g,
        q = window.location.hash.substring(1);
    while ( e = r.exec(q)) {
       hashParams[e[1]] = decodeURIComponent(e[2]);
    }
    return hashParams;
  }

  getTopArtists() {
    let array = [];
    spotifyApi.getMyTopArtists()
    .then(resp => {
      resp.items.forEach(artists => {
        array.push(artists.name);
      });
      this.setState({
        artistInfo: {
          names: array
        }
      });
    })
  }

  setArtistId(artist){
    fetch(`https://api.songkick.com/api/3.0/search/artists.json?apikey=${apiKey}&query=${artist}`)
    .then(resp => resp.json())
    .then(data => {
      try {
        this.setState({
          artistLookUp: {
            id: data.resultsPage.results.artist[0].id
          }
        })
        console.log(data);
      } catch(err) {
        console.log('Artist cannot be found');
      }
    })
    .then(() => this.getArtistEvent())
  }

  getArtistEvent() {
    fetch(`https://api.songkick.com/api/3.0/artists/${this.state.artistLookUp.id}/calendar.json?apikey=${apiKey}`)
    .then(resp => resp.json())
    .then(data => {
      try {
        this.setState({
          artistLookUp: {
            name: data.resultsPage.results.event[0].displayName,
            type: data.resultsPage.results.event[0].type,
            city: data.resultsPage.results.event[0].location.city,
            url: data.resultsPage.results.event[0].uri,
            lat: data.resultsPage.results.event[0].location.lat,
            lng: data.resultsPage.results.event[0].location.lng
          }
        })
      } catch(err) {
        alert(`Can't find next concert :(`);
      }
    })
  }

  loadNames() {
    return(this.state.artistInfo.names.map(artist => {
      return(<ListItem key={artist} id='list' button onClick={() => this.setArtistId(artist)}>
              <ListItemText primary={artist}/>
             </ListItem>);
    }))
  }

  addName() {
    this.setState({
      artistInfo: {
        names: [this.state.value, ...this.state.artistInfo.names]
      }
    })
  }

  render() {
    return (
      <div className="App" id="map">
      <a href='http://ec2-54-158-49-246.compute-1.amazonaws.com/login'><Button variant='contained' color='primary'>Login With Spotify</Button></a>
        <div id='container'>
          <div id='info'>
            <TextField label='Search For An Artist' onChange={(e) => this.setState({value: e.target.value})}></TextField>
            <Button variant='contained' onClick={() => this.setArtistId(this.state.value)}>Next Concert</Button>
            <Button variant='contained' onClick={() => this.addName()}>Add</Button>
          </div>
          <List component='nav'>
            {this.loadNames()}
          </List>
          <GoogleMapsContainer id='googleMap' location={this.state.artistLookUp}/>
        </div>
      </div>
    );
  }
}

export default Main;



